body {
  overflow: hidden;
  background: rgb(241, 241, 241);
}

#status-bar-fake{
  height: 24px;
  background: white;
}

.App-header {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  width: calc(100% - 20px);
  height: 60px;
  padding: 0 10px;
  background: white;
}

.header-item{
  margin-bottom: 12px;
}

#header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-content {
  position: absolute;
  top: 60px;
  left: 0;
  right: 0;
  bottom: 60px;
  padding: 10px;
  overflow-y: auto;
}

.main-content.mobile-scroll{
  top: 84px;
}

#bottom-navigation {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

#bottom-navigation-container {
  display: flex;
  height: 60px;
  align-items: center;
}

.nav-btn {
  width: 25%;
  height: 100%;
  border: none;
  border-top: 1px solid rgb(215, 215, 215);
  background: white;
}

.nav-btn.active {
  background: red;
}

.scroll-content {
  display: flex;
  overflow-x: auto;
}

.flex-content {
  display: flex;
  flex-wrap: wrap;
}

#logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#app-logo {
  width: 110px;
  height: 110px;
  border-radius: 20px;
}

#app-title {
  width: 100%;
  font-size: 20px;
  text-align: center;
  /* text-align: right; */
  padding: 15px 5px;
}

/* CARD */

.card {
  margin: 5px;
  overflow: hidden;
  border-radius: 5px;
  background: red;
}

.card-img {
  width: 100%;
  height: 170px;
  object-fit: cover;
}

.cart {
  width: calc(100% - 20px);
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 15px;
  box-shadow: 0 1px 7px 1px rgba(0, 0, 0, 0.1);
  background: white;
}

.cart-row{
  display: flex;
  flex-direction: row;
}

.cart-sub-row{
  padding-left: 15px;
}

.cart-img {
  width: 130px;
  height: 130px;
  border-radius: 10px;
}

.sticky {
  top: 0px;
  position: sticky;
}


.dialog{
  z-index: 99;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.75);
}

.dialog.show{
  display: block;
}

.dialog-main{
  position: absolute;
   top: 50%;
   transform: translateY(-55%);
}

.dialog-title{
  width: 100%;
  text-align: center;
  color: white;
}

.dialog-content{
  height: 410px;
  overflow-x: scroll;

}

.dialog-btn{
  width: calc(100% - 20px);
  padding: 15px 0;
  margin: 3px 10px;
}